
import { defineComponent, reactive, watchEffect } from "vue";

export default defineComponent({
  name: "custom-button",
  props: {
    label: String,
    isReverse: {
      type: Boolean,
      default: false,
    },
  },
  setup(props) {
    const buttonStyle = reactive({
      button__wrapper: true,
      reverse: false,
    });
    watchEffect(() => {
      if (props.isReverse) {
        buttonStyle.reverse = true;
      }
    });
    return { buttonStyle };
  },
});
